@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;1,900&display=swap);
.topbar-layout{background-color:#444444;overflow:hidden;top:0px;position:-webkit-sticky;position:sticky;list-style-type:none;display:flex;justify-content:space-evenly;height:5vh;margin:0px;z-index:9999}.topbar-layout--item{border:none;background-color:#444444;color:whitesmoke;font-weight:bold;width:100%;text-align:center;text-decoration:none;font-family:'Roboto', sans-serif;font-size:x-large}.topbar-layout--item:hover{background-color:#44b8bb;transition:background-color 0.2s ease-in;cursor:pointer}.topbar-layout--item:active{background-color:#369294;transition:background-color 0.05s ease-in}

.self-introduction{display:flex;flex-direction:column;align-items:center;font-family:'Roboto', sans-serif;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);color:#444966}.self-introduction--header{font-size:3vw;font-weight:bold;font-family:'Roboto', sans-serif}.self-introduction--body{font-size:2vw;font-family:'Roboto', sans-serif;position:relative;white-space:nowrap;margin-bottom:0}.self-introduction--social-platform-icon{margin:0px 2vw 0vw 2vw}.intro-page{width:100%;height:100%;position:absolute}


